import Rellax from "rellax";

const inevitabilityBlock = document.querySelector(".inevitability-block");

if (inevitabilityBlock) {
  const parallaxImage_1 = inevitabilityBlock.querySelector(".inevitability-block__founder-image");
  const parallaxImage_2 = inevitabilityBlock.querySelector(".inevitability-block__scientist");
  const parallaxImage_3 = inevitabilityBlock.querySelector(".inevitability-block__ellips");

  new Rellax(parallaxImage_1);
  new Rellax(parallaxImage_2);
  new Rellax(parallaxImage_3);
}
